import { create_canvas } from "../checker/mod";
import type { Loading } from "../loader/mod";
import { type Biome, biome } from "../biome/mod";
import { global_mob_data, rarity_color, sbio_progress } from "../mob/mod";

class Renderer {
    canvas: HTMLCanvasElement;
    ctx: CanvasRenderingContext2D;
    time: number;
    delta_time: number;
    screen_wrap_alpha: number;
    ff: number;
    center_x: number;
    center_y: number;
    cursor_x: number;
    cursor_y: number;
    debug: boolean;
    scroll_rv: number;
    scroll: number;
    left_click: boolean;
    cursor_state: boolean;
    progress: number;
    progress_rv: number;

    constructor() {

        this.canvas = create_canvas();
        this.ctx = this.canvas.getContext("2d") as CanvasRenderingContext2D;

        this.center_x = 0;
        this.center_y = 0;

        this.resize();

        this.cursor_x = 0;
        this.cursor_y = 0;

        this.time = 0;
        this.delta_time = 0;

        this.screen_wrap_alpha = 1.0;
        this.ff = 1000 / 60 * 0.01;

        this.debug = true;

        this.scroll = 0;
        this.scroll_rv = 0;

        this.left_click = false;
        this.cursor_state = false;

        this.progress = 0;
        this.progress_rv = 0;

    }

    clear(ctx: CanvasRenderingContext2D) {

        ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);

    }

    cover(ctx: CanvasRenderingContext2D) {

        ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);

    }

    wheel(y: number) {

        const v = y * (innerHeight / 735);

        this.scroll = Math.max(
            -500,
            Math.min(
                0,
                this.scroll - v
            )
        );

    }

    resize() {
        
        this.canvas.width = innerWidth * devicePixelRatio;
        this.canvas.height = innerHeight * devicePixelRatio;

        [
            this.center_x,
            this.center_y
        ] = this.get_center_position();

    }

    mouse_down(button: number) {

        if (button !== 0) return;

        this.left_click = true;

    }

    get_center_position() {

        return [
            this.canvas.width * 0.5,
            this.canvas.height * 0.5
        ];

    }

    mousemove(
        x: number,
        y: number
    ) {

        this.cursor_x = x * devicePixelRatio;
        this.cursor_y = y * devicePixelRatio;

    }

    draw_screen_wrapper(
        ctx: CanvasRenderingContext2D,
        loading: Loading
    ) {

        if (
            !loading.main && this.screen_wrap_alpha > 0
        ) {

            this.screen_wrap_alpha -= this.ff * this.delta_time;

            if (this.screen_wrap_alpha < 0) {
                return;
            }

            ctx.globalAlpha = this.screen_wrap_alpha;
            ctx.fillStyle = "#191919";
            this.cover(ctx);

        }

    }

    draw_test(
        ctx: CanvasRenderingContext2D
    ) {

        ctx.save();
        ctx.scale(0.5, 0.5);
        ctx.translate(100, 100 + this.scroll_rv);

        this.cursor_state = false;

        // i don wanna use brain to fix!
        for (const [_, v] of global_mob_data) {
            v.updated = false;
        }

        this.draw_biome_mobs(ctx, biome.garden);
        this.draw_biome_mobs(ctx, biome.desert);
        this.draw_biome_mobs(ctx, biome.ocean);
        this.draw_biome_mobs(ctx, biome.jungle);
        this.draw_biome_mobs(ctx, biome.ant_hell);
        this.draw_biome_mobs(ctx, biome.sewers);
        this.draw_biome_mobs(ctx, biome.factory);
        this.draw_biome_mobs(ctx, biome.pyramid);

        this.progress_rv += (this.progress - this.progress_rv) * 0.01 * this.delta_time;

        ctx.restore();

        ctx.save();
        ctx.translate(-500, -100);

        ctx.globalAlpha = 1;
        ctx.fillStyle = "#191919";
        ctx.strokeStyle = "#191919";
        ctx.lineWidth = 10;
        const path = new Path2D;
        ctx.translate(this.canvas.width - 10, this.canvas.height - 10);
        path.roundRect(0, 0, 490, 50, 50);
        const yajuandu = Math.min(1, this.progress_rv / 50);
        const len = 480 * yajuandu;
        ctx.fill(path);
        ctx.globalAlpha = Math.min(1, len / 40);
        ctx.roundRect(5, 5, Math.max(40, len), 40, 40);
        ctx.fillStyle = "#e2f275";
        ctx.fill();
        ctx.beginPath();
        ctx.clip(path);
        ctx.stroke(path);

        ctx.globalAlpha = 1;
        ctx.lineWidth = 2.5;
        ctx.fillStyle = "#fff";
        ctx.strokeStyle = "#191919";
        ctx.font = "bold 25px Ubuntu";
        ctx.textAlign = "center";
        ctx.textBaseline = "middle";

        const p = `${Math.min(100, Math.round(yajuandu * 100))}%`;

        ctx.save();
        ctx.translate(245, 25);
        ctx.strokeText(p, 0, 0);
        ctx.fillText(p, 0, 0);
        ctx.restore();

        ctx.restore();

    }

    draw_biome_mobs(
        ctx: CanvasRenderingContext2D,
        area: Biome
    ) {

        ctx.save();
        ctx.save();

        ctx.fillStyle = area.color;
        ctx.lineWidth = 30;

        const path = new Path2D;
        const w = (area.mobs.length + 1) * 120;
        path.roundRect(0, -50, w, 250, 10);

        ctx.fill(path);
        ctx.beginPath();

        ctx.clip(path);
        ctx.globalAlpha = 0.2;
        ctx.fillStyle = "#191919";
        ctx.save();
        ctx.stroke(path);
        ctx.beginPath();

        ctx.restore();
        ctx.restore();

        ctx.globalAlpha = 1;
        ctx.lineWidth = 5;
        ctx.fillStyle = "#fff";
        ctx.strokeStyle = "#191919";
        ctx.font = "bold 55px Ubuntu";
        //ctx.textAlign = "center";
        ctx.letterSpacing = "1px";

        ctx.save();
        ctx.translate(60, 20);
        ctx.strokeText(area.name, 0, 0);
        ctx.fillText(area.name, 0, 0);
        ctx.restore();

        ctx.translate(-60, 50);

        const bg_path = new Path2D;

        bg_path.roundRect(0, 0, 110, 110, 10);

        for (const mob of area.mobs) {

            const r = rarity_color.get(mob.rarity);

            ctx.translate(120, 0);

            ctx.save();

            (() => {

                if (ctx.isPointInPath(bg_path, this.cursor_x, this.cursor_y)) {
                    this.canvas.style.cursor = "pointer";
                    this.cursor_state = true;
                    if (!this.left_click) return;
                    mob.defeated = !mob.defeated;
                    this.progress = sbio_progress();
                    return;
                }

                if (this.cursor_state) return;

                this.canvas.style.cursor = "default";

            })();

            if (!mob.updated) {
                mob.updated = true;
                mob.defeated_time += (
                    (
                        mob.defeated ? 1 : 0
                    )
                    - mob.defeated_time
                ) * 0.02 * this.delta_time;
            }

            ctx.globalAlpha = 0.5 + (Math.abs(mob.defeated_time - 1) / 1) * 0.5;

            if (r === undefined) {
                throw new Error(`${mob.rarity} does not exist`);
            }

            ctx.lineWidth = 20;
            ctx.fillStyle = r;
            ctx.strokeStyle = "#191919";
            ctx.fill(bg_path);
            ctx.drawImage(mob.image, 0, 0);
            ctx.globalAlpha = ctx.globalAlpha * 0.2;
            ctx.clip(bg_path);
            ctx.stroke(bg_path);
            ctx.restore();

            ctx.save();

            ctx.translate(-30, 15);

            ctx.translate(60, 60);
            ctx.rotate(Math.PI * 0.25);
            ctx.translate(-60, -60);

            ctx.globalAlpha = 1;
            ctx.fillStyle = "#a5e381";

            ctx.roundRect(20, 20, 90 * mob.defeated_time, 15, 50);
            ctx.fill();
            ctx.beginPath();
            ctx.translate(60, 60);
            ctx.rotate(-Math.PI * 0.5);
            ctx.translate(-60, -60);
            ctx.roundRect(50, 56, 90 * mob.defeated_time, 15, 50);
            ctx.fill();
            ctx.beginPath();

            ctx.restore();

        }
        ctx.restore();

        ctx.translate(0, 280);

        ctx.globalAlpha = 1;

    }

    draw(
        time: number,
        loading: Loading
    ) {

        this.delta_time = time - this.time;
        this.time = time;

        this.scroll_rv += (this.scroll * (735 / innerHeight) - this.scroll_rv) * 0.01 * this.delta_time;

        const ctx = this.ctx;
        ctx.globalAlpha = 1.0;

        this.clear(ctx);

        ctx.fillStyle = "#b1cbe6";
        this.cover(ctx);

        this.draw_screen_wrapper(ctx, loading);
        this.draw_test(ctx);

        this.left_click = false;

    }

}

export {
    Renderer
}