import { type Mob, global_mob_data } from "../mob/mod";

class Biome {
    name: string;
    color: string;
    mobs: Mob[];
    constructor(
        name: string,
        color: string,
        mob_list: string[],
    ) {

        this.name = name;
        this.color = color;
        this.mobs = [];

        for (const sid of mob_list) {

            const data = global_mob_data.get(sid);

            if (data === undefined) {
                throw new Error(`${sid} does not exist.`);
            }

            this.mobs.push(
                data
            );
        }

    }
}

