
import "./main.css";

import { Renderer } from "./renderer/mod";
import { Loading } from "./loader/mod";

const start = async () => {

    const loading = new Loading;
    const renderer = new Renderer;

    addEventListener("resize", () => {
        renderer.resize();
    });
    addEventListener("mousemove", event => {

        renderer.mousemove(
            event.clientX,
            event.clientY
        );

    });
    addEventListener("mousedown", event => {

        renderer.mouse_down(event.button);

    })
    addEventListener("wheel", event => {

        renderer.wheel(
            event.deltaY
        );

    })
    addEventListener("load", () => {
        loading.main = false;
    });

    const render_loop = (time: number) => {

        renderer.draw(
            time,
            loading
        );

        requestAnimationFrame(render_loop);

    };

    requestAnimationFrame(render_loop);

    console.info("114514");

};

start();