class Mob {
    id: number;
    image: HTMLImageElement;
    rarity: number;
    defeated: boolean;
    defeated_time: number;
    updated: boolean;
    constructor(
        id: number,
        image: HTMLImageElement,
        rarity: number
    ) {

        this.id = id;
        this.image = image;
        this.rarity = rarity;
        this.defeated = false;
        this.defeated_time = 0;
        this.updated = false;

    }
}

const global_mob_data: Map<string, Mob> = new Map;

for (const [k, v] of mob_data) {
    global_mob_data.set(
        k,
        new Mob(v.id, v.image, v.rarity)
    );
}

const rarity_color = new Map([
    [
        0,
        "#7eef6d"
    ],
    [
        1,
        "#ffe65d"
    ],
    [
        2,
        "#4d52e3"
    ],
    [
        3,
        "#861fde"
    ],
    [
        4,
        "#de1f1f"
    ],
    [
        5,
        "#1fdbde"
    ],
    [
        6,
        "#ff2b75"
    ],
    [
        7,
        "#2bffa3"
    ],
    [
        9,
        "#555"
    ]
]);

const serializer = new XMLSerializer;
const svg_to_url = (svg: string) => {

    const temp = document.createElement("div");

    temp.innerHTML = svg;

    const yjsnpi = serializer.serializeToString(
        temp.firstElementChild as Node
    );

    temp.remove();

    return URL.createObjectURL(
        new Blob([
            yjsnpi
        ], {
            type: "image/svg+xml"
        })
    );

}

for (const [_, data] of mob_data) {

    data.image.src = svg_to_url(data.svg);

}

const sbio_progress = () => {

    let point = 0;

    for (const [_, v] of global_mob_data) {

        if (!v.defeated) continue;

        point++;

    }

    return point;

};

export { global_mob_data, rarity_color, type Mob, sbio_progress };